* {
  font-family: "Comic Sans MS", "Tahoma";
}

.wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh
}

.title {
  font-size: 42px;
}

.sub-title {
    font-size: 24px;
    color: rgba(0,0,0,.3);
}

.array {
  margin-top: 44px;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

.item {
  font-size: 26px;
  width: 40px;
  height: 40px;
  background: #bffbff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  margin-left: 10px;
  margin-top: 10px;
  border-radius: 6px;
}

.item:first-child {
    margin-left: 0;
}

.item.active {
    box-shadow: 0 0 6px 4px rgba(255, 255, 0, .5);
    background: rgba(255, 255, 0, .9);
}

.finger {
    width: 150px;
    transform: rotate(90deg);
    margin-top: 20px;
}

.press-finger {
    transform: translate3d(-10px, -20px, 0) rotate(90deg);
}

.button.disable {
  background: #999;
}

.points {
  margin-left: 16px;
  color: #ca57ff;
  display: inline-block;
}
